.about-us {
    display: flex;
  position: relative;
  margin: 100px 0;

  .left-display {
      width: 50%;

    h1 {
      font-style: italic;
    }

    img {
      width: 80%;
      margin-left: 100px;
      margin-top: 30px;
      border-radius: 20px;  
    }
  }

  .right-display {
    width: 50%;
    padding-top: 140px;
    margin-left: 70px;
    // bottom: 0px;
    // left: 350px;
    @media (max-width: 880px) {
        bottom: 20px;
        left: 40px;
    }
    ul {
      overflow: hidden;
      li {
          padding: 10px;
        list-style: none;
        h2 {
          font-size: 100%;
          font-weight: normal;
          font-family:fantasy;

        }
        p {
          font-size: 100%;
          font-weight: normal;
          font-family: fantasy;
        }
        .rounded-container {
          text-decoration: none;
          color: #000;
          background:whitesmoke;
          display: block;
          flex-direction: column;
          height: 300px;
          width: 500px;
          padding: 30px;
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          border-radius: 20px;
          -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        }
      }
    }
  }




}
